@import "../../assets/scss/app";

.vote {
  //overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 31px;
  padding-left: 20px;
  padding-right: 20px;

  @include desktop {
    padding-top: 121px;
    padding-left: 0;
    padding-right: 0;
  }

  .groupMobile {
    @include onlyMobile;
    position: absolute;
    z-index: 1;
    top: 55px;
    left: 50%;
    transform: translateX(-50%);
    width: 320px;
  }

  .groupDesktop {
    @include onlyDesktop;
    position: absolute;
    z-index: 1;
    top: -360px;
    left: 50%;
    transform: translateX(-50%);
    width: 1825px;
  }

  .title {
    position: relative;
    z-index: 2;
    @include b2-title;
    font-size: 40px;
    line-height: 49px;
    text-align: center;
    color: #000000;

    @include desktop {
      font-size: 100px;
      line-height: 123px;
      width: 706px;
      margin-left: auto;
      margin-right: auto;
    }

  }

  .mainMobile {
    @include onlyMobile();
    position: absolute;
    z-index: 3;
    width: 320px;
    height: 168px;
    top: 70px;
    left: 50%;
    transform: translateX(-50%);
  }

  .mainDesktop {
    @include onlyDesktop();
    position: absolute;
    z-index: 3;
    width: 1050px;
    height: 550px;
    top: 318px;
    left: 50%;
    transform: translateX(-50%);
  }

  .text {
    margin-top: 156px;
    position: relative;
    z-index: 4;
    font-size: 30px;
    line-height: 37px;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
    @include b2-text-mobile;

    @include desktop {
      margin-top: 461px;
      font-size: 60px;
      line-height: 74px;
      width: 924px;
      margin-left: auto;
      margin-right: auto;
      @include b2-text-desktop;
    }
  }

  .voteBtn {
    @include button;
    @include centerRow;
    @include b2-vote-btn;
    position: relative;
    z-index: 4;
    width: 100%;
    height: 80px;
    box-sizing: border-box;
    background: radial-gradient(5.12% 19.92% at 92.14% 90.6%, rgba(239, 246, 33, 0.8) 0%, rgba(239, 246, 33, 0) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, radial-gradient(5.71% 38.74% at 98.57% 49.57%, #499000 0%, rgba(73, 144, 0, 0) 100%), radial-gradient(5.24% 22.38% at 2.14% 30.77%, rgba(239, 246, 33, 0.8) 0%, rgba(239, 246, 33, 0) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, radial-gradient(9.67% 65.38% at 5% 91.03%, #499000 0%, rgba(73, 144, 0, 0) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, linear-gradient(180deg, #458D00 0%, #488D00 15.62%, #72B903 44.27%, #71B902 68.75%, #71BA02 100%);
    border: 5px solid #0E280E;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    margin-top: 36px;

    @include desktop {
      margin-top: 66px;
      width: 352px;
      margin-left: auto;
      margin-right: auto;
    }

    p {
      font-weight: 400;
      font-size: 30px;
      line-height: 37px;
      color: #000000;
    }
  }


  .btns {
    position: relative;
    z-index: 4;
    align-self: stretch;
    margin-top: 15px;
    display: flex;
    flex-direction: column;

    @include desktop {
      margin-top: 53px;
      flex-direction: row;
      justify-content: center;
    }

    .btn {
      @include button;
      @include centerRow;
      width: 100%;
      height: 100px;
      background: radial-gradient(5.12% 19.92% at 92.14% 90.6%, rgba(239, 246, 33, 0.8) 0%, rgba(239, 246, 33, 0) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, radial-gradient(5.71% 38.74% at 98.57% 49.57%, #499000 0%, rgba(73, 144, 0, 0) 100%), radial-gradient(5.24% 22.38% at 2.14% 30.77%, rgba(239, 246, 33, 0.8) 0%, rgba(239, 246, 33, 0) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, radial-gradient(9.67% 65.38% at 5% 91.03%, #499000 0%, rgba(73, 144, 0, 0) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, linear-gradient(180deg, #458D00 0%, #488D00 15.62%, #72B903 44.27%, #71B902 68.75%, #71BA02 100%);
      border: 5px solid #0E280E;
      box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
      border-radius: 100px;

      @include desktop {
        width: 448px;
      }

      &:last-child {
        margin-top: 15px;

        @include desktop {
          margin-top: 0;
          margin-left: 32px;
        }
      }

      p {
        @include b2-btn;
        font-size: 50px;
        line-height: 62px;
        color: #000000;
      }
    }
  }

  .socialLinks {
    display: flex;
    justify-content: center;
    margin-top: 28px;

    @include desktop {
      margin-top: 66px;
    }

    .link {
      display: block;

      &:not(:first-child) {
        margin-left: 4px;

        @include desktop {
          margin-left: 40px;
        }
      }

      img {
        display: block;
        width: 56.76px;
        height: 56.39px;

        @include desktop {
          width: 153px;
          height: 150px;
        }
      }
    }
  }

}
