@import "../../assets/scss/app";

.plan {
  position: relative;
  padding-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include desktop {
    padding-top: 73px;
  }

  .mobile1 {
    @include onlyMobile;
    position: absolute;
    z-index: 1;
    top: 200px;
    left: 50%;
    transform: translateX(-50%);
    width: 320px;
  }

  .mobile2 {
    @include onlyMobile;
    position: absolute;
    z-index: 1;
    top: 615px;
    left: 50%;
    transform: translateX(-50%);
    width: 320px;
  }

  .mobile3 {
    @include onlyMobile;
    position: absolute;
    z-index: 1;
    top: 1030px;
    left: 50%;
    transform: translateX(-50%);
    width: 320px;
  }

  .mobile4 {
    @include onlyMobile;
    position: absolute;
    z-index: 1;
    top: 1445px;
    left: 50%;
    transform: translateX(-50%);
    width: 320px;
  }


  .logo {
    position: relative;
    z-index: 2;
    display: block;
    width: 300px;
    height: 94.55px;

    @include desktop {
      width: 514px;
      height: 162px;
    }
  }

  .title {
    @include b4-title;
    position: relative;
    z-index: 2;
    margin-top: 20px;
    font-size: 60px;
    line-height: 74px;
    text-align: center;
    color: #000000;

    @include desktop {
      margin-top: 0;
      font-size: 100px;
      line-height: 123px;
    }
  }

  .pots {
    position: relative;
    z-index: 2;
    margin-top: 71px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop {
      margin-top: 86px;
      flex-direction: row;
      justify-content: center;
    }

    .pot {
      position: relative;
      height: 391px;

      @include desktop {
        height: 461px;
      }

      &:not(:first-child) {
        margin-top: 91px;

        @include desktop {
          margin-top: 0;
          margin-left: 78px;
        }
      }

      &:nth-child(2) {
        @include desktop {
          margin-top: 80px;
        }
      }

      img {
        display: block;
        height: 100%;
        width: auto;
      }

      .label, .text {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        text-align: center;
        color: #000000;
        width: 100%;
      }

      .label {
        @include b7-label;
        font-size: 50px;
        line-height: 62px;
        text-transform: uppercase;
        white-space: nowrap;

        @include desktop {
          top: -40px;
        }
      }

      .text {
        @include b7-text;
        font-size: 30px;
        line-height: 40px;
        width: 230px;

        @include desktop {
          width: 240px;
        }

      }

      &:nth-child(1) {
        .text {
          bottom: 12px;

          @include desktop {
            bottom: 70px;
          }
        }
      }

      &:nth-child(2) {
        .text {
          bottom: 64px;

          @include desktop {
            bottom: 71px;
          }
        }
      }

      &:nth-child(3) {
        .text {
          bottom: -5px;

          @include desktop {
            bottom: 55px;
          }
        }
      }

      &:nth-child(1), &:nth-child(3) {
        .label {
          top: -35px;

          @include desktop {
            top: -40px;
          }
        }
      }

      &:nth-child(2) {
        .label {
          top: -12px;
        }
      }
    }
  }
}
