@import "../../assets/scss/app";

.tokenomics {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 40px;
  position: relative;

  @include desktop {
    padding-top: 75px;
  }

  .mobile1 {
    @include onlyMobile();
    position: absolute;
    z-index: 1;
    top: -30px;
    left: 50%;
    width: 320px;
    transform: translateX(-50%);
  }

  .mobile2 {
    @include onlyMobile();
    position: absolute;
    z-index: 1;
    top: 800px;
    left: 50%;
    width: 320px;
    transform: translateX(-50%);
  }

  .mobile3 {
    @include onlyMobile();
    position: absolute;
    z-index: 1;
    top: 380px;
    left: 50%;
    width: 320px;
    transform: translateX(-50%);
  }

  .title {
    position: relative;
    z-index: 3;
    @include b4-title;
    font-size: 60px;
    line-height: 74px;
    text-align: center;
    text-transform: uppercase;
    color: #000000;

    @include desktop {
      font-size: 150px;
      line-height: 185px;
    }
  }

  .pots {
    position: relative;
    z-index: 2;
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;


    @include desktop {
      margin-top: 36px;
      flex-direction: row;
      justify-content: center;
    }

    .pot {
      position: relative;
      width: 252px;
      height: 349.02px;

      @include desktop {
        width: 387px;
        height: 536px;
      }

      &:not(:first-child) {
        margin-top: 35px;

        @include desktop {
          margin-top: 0;
          margin-left: 84px;
        }
      }

      img {
        display: block;
        width: 252px;
        height: 349.02px;

        @include desktop {
          width: 387px;
          height: 536px;
        }
      }

      .label {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);

        font-weight: 400;
        font-size: 32.5581px;
        line-height: 40px;
        color: #000000;
        text-align: center;
        @include b6-label-mobile;

        @include desktop {
          font-size: 50px;
          line-height: 62px;
          @include b6-label-desktop;
        }
      }

      &:nth-child(1) {
        p {
          bottom: 35px;
          width: 100%;

          @include desktop {
            bottom: 54px;
            width: 100%;
          }
        }
      }

      &:nth-child(2) {
        p {
          bottom: 12px;
          width: 155px;

          @include desktop {
            bottom: 20px;
            width: 238px;
          }
        }
      }

    }
  }

  .text {
    @include b5-text-mobile;
    position: relative;
    z-index: 2;
    margin-top: 35px;
    font-size: 20px;
    line-height: 25px;
    text-align: center;
    color: #000000;

    @include desktop {
      @include b5-text-desktop;
      margin-top: 75px;
      width: 735px;
      margin-left: auto;
      margin-right: auto;
      font-size: 40px;
      line-height: 49px;
    }
  }

}
